import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, WindowComponent } from 'summer';
import { Cbo, CLASS_NAME } from '../../../../models/dtos/mfp/cbo';

interface CboEditViewProps extends EditViewProps<Cbo> {}

const CboEditView: FC<CboEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Cbo>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });
  return (
    <WindowComponent title="Manutenção de CBO">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline
          formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 6), required: true } }}
          id="codigo"
          isPrependLabel={true}
          label="Código"
          placeholder="Informe o Código do CBO."
        />
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true, maxLength: 250 } }}
          id="descricao"
          isPrependLabel={true}
          label="Descrição"
          placeholder="Informe a descrição."
          type={InputType.TEXT_AREA}
        />
        <Row>
          <Col>
            <CustomInput type="switch" id="professor" name="professor" label="Salário aula" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput
              type="switch"
              id="motoristaProfissional"
              name="motoristaProfissional"
              label="É motorista profissional?"
              innerRef={formProps.register}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CboEditView;
